<template>
  <div class="profile-view">
    <h1>My Profile</h1>
    
    <!-- Success Message -->
    <div v-if="updateStatus" class="alert success">
      {{ updateStatus }}
    </div>

    <div class="profile-content">
      <div class="user-info">
        <h2>Personal Information</h2>
        <form @submit.prevent="updateProfile">
          <div class="form-group">
            <label>Full Name</label>
            <input type="text" v-model="userInfo.fullName" required>
          </div>

          <div class="form-group">
            <label>Email</label>
            <input type="email" v-model="userInfo.email" disabled>
          </div>

          <div class="form-group">
            <label>Phone</label>
            <input type="tel" v-model="userInfo.phone">
          </div>

          <div class="form-group">
            <label>Address</label>
            <textarea v-model="userInfo.address"></textarea>
          </div>

          <button type="submit">Update Profile</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'

const store = useStore()
const userInfo = ref({
  fullName: '',
  email: '',
  phone: '',
  address: ''
})

const updateStatus = computed(() => store.getters['user/getUpdateStatus'])

const updateProfile = async () => {
  try {
    await store.dispatch('user/updateUser', userInfo.value)
    setTimeout(() => {
      store.commit('user/SET_UPDATE_STATUS', null)
    }, 3000)
  } catch (error) {
    console.error('Update failed:', error)
  }
}

onMounted(() => {
  const currentUser = store.getters['auth/currentUser']
  userInfo.value = {
    fullName: currentUser.fullName || '',
    email: currentUser.email || '',
    phone: currentUser.phone || '',
    address: currentUser.address || ''
  }
})
</script>

  <style scoped>
  .profile-view {
    margin-top: 30px;
    max-width: 800px;
    padding: 2rem;
  }
  
  h1 {
    color: var(--text-color);
    margin-bottom: 2rem;
    text-align: center;
  }
  
  .profile-content {
    background: var(--light-bg);
    border-radius: var(--radius-md);
    box-shadow: var(--shadow-md);
    padding: 2rem;
  }
  
  .user-info h2 {
    color: var(--primary-color);
    margin-bottom: 1.5rem;
  }
  
  .form-group {
    margin-bottom: 1.5rem;
  }
  
  label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 500;
  }
  
  input, textarea {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ddd;
    border-radius: var(--radius-sm);
    font-size: var(--text-md);
  }
  
  textarea {
    resize: vertical;
  }
  
  button {
  background: #3498db;
  color: white;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: var(--radius-sm);
  font-size: var(--text-md);
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 25%;
  margin-top: 1rem;
  margin-left: 580px;
  font-weight: bold;
  display: block;
}

.alert {
  padding: 1rem;
  border-radius: var(--radius-sm);
  margin-bottom: 1rem;
  text-align: center;
}

.success {
  background-color: var(--secondary-color);
  color: white;
}
  </style>
  